import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import LoadingOverlay from "react-loading-overlay";
import {
  Grid,
  Card,
  MuiThemeProvider,
  TablePagination,
} from "@material-ui/core";
import routes from "../../routes/routes";

const VendorList = (props) => {
  const [page, setPage] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "dataIndex",
      label: "Number",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "vendorName",
      label: "Vendor Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "vendorDescription",
      label: "Vendor Description",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "vengorImage",
      label: "Vendor Image",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "contactName",
      label: "Contact Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "email",
      label: "Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "orderEmail",
      label: "Order Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "orderFax",
      label: "Order Fax",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "phone",
      label: "Phone",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let productData = [];

  const getAllVendors = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.vendor.getAllVendors.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [product] = await axios.all([pData]);

    setTotalCount(product.data.totalDoc);

    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        vendorName: data.vendorName,
        vendorDescription: data.vendorDescription,
        vengorImage: data.vengorImage,
        contactName: data.contactName,
        email: data.email,
        orderEmail: data.orderEmail,
        orderFax: data.orderFax,
        phone: data.phone,
      };
      productData = [...productData, newdata];
      return productData;
    });
    setWarehouses(productData);
    setIsActive(false);
  };

  useEffect(() => {
    getAllVendors();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div>
        <div>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={warehouses}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </div>
      </div>
    </LoadingOverlay>
  );
};
export default VendorList;
