import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import LoadingOverlay from "react-loading-overlay";
import { Grid, MuiThemeProvider, Card, TablePagination } from "@material-ui/core";
import routes from "../../routes/routes";

const CategoryList = (props) => {
  const [page, setPage] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "dataIndex",
      label: "Number",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "categoryName",
      label: "Category Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "categoryDescription",
      label: "Category Category",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "categoryImage",
      label: "Category Image",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "parentCategory",
      label: "Parent Category",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let productData = [];

  const getAllCategories = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.category.getAllCategories.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [product] = await axios.all([pData]);
    setTotalCount(product.data.totalDoc);

    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        categoryName: data.categoryName,
        categoryDescription: data.categoryDescription,
        categoryImage: data.categoryImage,
        parentCategory: data.parentCategory,
      };
      productData = [...productData, newdata];
      return productData;
    });

    setWarehouses(productData);
    setIsActive(false);
  };

  useEffect(() => {
    getAllCategories();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div>
        <div>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={warehouses}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </div>
      </div>
    </LoadingOverlay>
  );
};

export default CategoryList;
